html {
    box-sizing: content-box;
}

html * {
    box-sizing: inherit;
}

header {
    display: flex;
    justify-content: center;
    background-color: #6698A5;
    padding: 22px;
    box-shadow: 1px 2px 5px rgba(7, 7, 7, 0.514);
    z-index: 2;
    position: relative;
}

header input{
    outline: none;
    border: solid 1px #eee;
    padding: 10px 5px;
    border-radius: 4px;
}